.crud {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.crud .crud-cont .cajas {
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  place-content: center;
}
@media (min-width: 768px) {
  .crud .crud-cont .cajas {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .crud .crud-cont .cajas {
    grid-template-columns: repeat(3, 1fr);
  }
}
.crud .crud-cont .cajas .caja {
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: #b61515;
  border-radius: 1rem;
  margin: auto;
  padding: 0.6rem;
}
.crud .crud-cont .cajas .caja p {
  color: #FFFBFE;
  text-transform: uppercase;
  text-decoration: underline;
  margin: auto;
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.crud .crud-cont .cajas .caja img {
  width: 100%;
  height: 30vh;
  margin: auto;
  object-fit: cover;
  border-radius: 1rem;
}
.crud .crud-cont .cajas .caja a {
  margin-top: 10vh;
  display: inline-block;
  color: #FFFBFE;
  background-color: #00aaff;
  width: 80%;
  margin: auto;
  padding: 0.6rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  font-size: 2rem;
}

