  display: flex;
  align-items: center;
  padding: 0px 6px;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  color: #000000;
  transition: 1s;
  border-radius: 10rem;
  transform: rotate(90deg);
  height: 10rem;
  background-color: #26628b;
  border: none;
  cursor: pointer;
}
@media (min-width: 1024px) {
